<template>
 
 
 
  <div class="q-pa-md row items-start q-gutter-md">
    <q-card  dark bordered class="bg-grey-5 my-card">
    <q-card-section>
        <q-chip color="orange" icon="fas fa-sms"> Message Cast </q-chip>
    </q-card-section>
    <q-card-section>
         <q-checkbox v-model="sendAll" color="teal">Send To All</q-checkbox>
        </q-card-section>
      <q-card-section>
                
        <q-select
          v-if="sendAll == false"
          filled
          label="Customer Numbers"
          v-model="contacts"
          use-input
          use-chips
          multiple    
          emit-value
          map-options
          input-debounce="0"
          @new-value="createValue"
          :options="filterOptions"
          @filter="filterFn"
          style="width: 250px"
        />
      </q-card-section>
      <q-card-section>
        <q-input v-model="message" filled type="textarea" label="Message" />
      </q-card-section>
      <q-card-section>
     <div  style="text-align: right">
       <q-btn color="teal" label="Send" @click="send()"></q-btn>
       </div>
      </q-card-section>
    </q-card>
  </div>
</template>
<script>
const axios = require("axios");
const token = localStorage.getItem("user-token");
const instance = axios.create({
  baseURL: "http://127.0.0.1:8000/api/",
  headers: {
    Authorization: "Bearer " + token,
  },
});
const stringOptions = []
export default {
  data() {
    return {
      sendAll: true,
      contacts: [],
      message: null,
      filterOptions: stringOptions,
    };
  },
  methods: {
    createValue(val, done) {
      if (val.length > 0) {
        if (!stringOptions.includes(val)) {
          stringOptions.push(val);
        }
        done(val, "toggle");
      }
    },
    filterFn(val, update) {
      update(() => {
        if (val === "") {
          this.filterOptions = stringOptions;
        } else {
          const needle = val.toLowerCase();
          this.filterOptions = stringOptions.filter(
            (v) => v.toLowerCase().indexOf(needle) > -1
          );
        }
      });
    },
    render(){
      instance.get('http://127.0.0.1:8000/api/messagecast/branch/index').then((res)=>{
        console.log(res)
          res.data.forEach(element => {
            stringOptions.push({label: element.name, val: element.number})
          });
      })
    },
    send(){
         var inden = 0;
         var all;
       if(this.sendAll == true){
          inden = 1
       }else{
         all = stringOptions;
       }
        let contact =  this.contacts;
        let message = this.message;
      instance.post('/messagecast/marketing/send',{
          stringOptions,
          contact,
          message,
          inden
      }).then((res)=>{
          console.log(res)
      },(err)=>{
        console.log(err)
      })
    }
  },
     mounted(){
      this.render();
    },
};
</script>